.gradient:focus {
  border: 1px solid transparent;
  background-image: linear-gradient(#141414, #141414), linear-gradient(43deg, #a162e8 0%, #f7ce68 46%, #85ffbd 100%);
  background-origin: border-box;
  background-clip: content-box, border-box;
}


#main {
  position: fixed;
  width: 100vw;
  top: 0;
  left: 0;
  height: 100vh;
  overflow: hidden;
  background-color: rgb(36, 34, 34);
}

.flex-container {
  display: flex;
  flex-direction: column;

}

.flex-item {
  width: 50%;

}
.flex-item-left {
  position: relative;
  height: 100%;
  width: 50%;
  display: flex;
  top: 0;
  justify-content: center;
  align-items: center;
  -webkit-perspective: 38rem;
          perspective: 38rem;
  z-index: 2;
}

.bg-container {
  position: absolute;
  right: 0;
  top: 0;
}

.hero {
  position: absolute;
  height: 10px;
  width: 600px;
  z-index: 1;
  top: 150px;

}

.flex-item-right {
  inset: 0px;
  width: 100%;
  height: 100%;
  position: fixed;
  overflow: auto;
}
.flex-item-right .inner-right-item {
  position: relative;
  width: 50%;
  left: 50%;
}
.inner-right-item {
  overflow: auto;
  -webkit-transform: translate(0px, 0px);
          transform: translate(0px, 0px);
}
.content-container {
  position: relative;
  max-width: 50rem;
}
.content-container .first-section {
  display: flex;
  align-items: center;
  margin-top: 300px;
  margin-bottom: 100px;
}
.content-container .section {
  position: relative;
  width: 100%;
  margin-bottom: 300px;
}
.content-container .section .container {
  display: block;
  width: 100%;
}

.resume {
  border-left: 0.1rem solid rgba(255, 255, 255, 0.2);
}
.resume .resume-item {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  margin-left: 5rem;
  position: relative;
  padding: 8rem 5rem;
  background: #181818;
  margin-bottom: 6rem;
  border-radius: 10px;
}
.resume .resume-item:before {
  content: "";
  position: absolute;
  left: 0;
  top: 20%;
  z-index: 11;
  -webkit-transform: translate(-50%, -50%) rotate(45deg);
  transform: translate(-50%, -50%) rotate(45deg);
  -webkit-transform-origin: center center;
  transform-origin: center center;
  width: 2rem;
  height: 2rem;
  background: inherit;
}
.resume .resume-item:after {
  content: "";
  position: absolute;
  left: -5rem;
  top: 20%;
  z-index: 11;
  -webkit-transform: translate(-100%, -50%);
  transform: translate(-100%, -50%);
  width: 1px;
  height: 40px;
  background: #fff;
}


.content-center {
  display: flex;
